
export default {
    showLoading: ({ commit }) => {
        commit('showLoading')
    },
    hideLoading: ({ commit }) => {
        commit('hideLoading')
    },
    showNav: ({ commit }) => {
        commit('showNav')
    },
    hideNav: ({ commit }) => {
        commit('hideNav')
    },
    setToken: ({
        commit
    },token) => {
        commit('setToken', token);
    },
  setUserId:({
    commit
  },userId)=>{
    commit('setUserId',userId);
  },
    clearToken:({
       commit
    })=>{
        commit('clearToken');
    },
  clearUserId:({
                commit
              })=>{
    commit('clearUserId');
  },
   /* hideLogin:({
        commit
    },hide)=>{
        commit('hideLogin',hide);
    },*/
    showLogin:({
        commit
    },sh)=>{
        commit('showLogin',sh);
    },

}



